
.btn{
    background-color: transparent;
    border: none;
    color: #fff;
    cursor: pointer;
    font-size: 12px;
    padding: 7px 10px;

    transition: color .3s ease;
    &:focus {
        outline: none;//retiro el borde
    }

    &:hover{
        color: darken($color:#fff, $amount: 30);
    }
}

.btn-primary{
    background-color:$honey-yellow;
    border-radius: 2px;

    &:disabled {
        background-color:lighten($color: $honey-yellow, $amount: 15);//con esto lo pongo mas claro
    }

    &:hover {
        background-color: darken($color: $honey-yellow, $amount: 15);//con esto lo pongo mas oscuro
    }
}

.btn-danger{
    background-color: red;

    transition: background-color 0.3s  ease ;

    &:hover {
        background-color: darken($color: red, $amount: 10);
    }
}

.btn-block{
    width: 100%;
}

.google-btn {
    cursor: pointer;
    margin-top: 5px;
    width: 100%;
    height: 42px;
    background-color: $google-blue;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);

    transition: box-shadow .3s ease;

    .google-icon-wrapper {
        position: absolute;
        margin-top: 1px;
        margin-left: 1px;
        width: 40px;
        height: 40px;
        border-radius: 2px;
        background-color: $white;
    }
    .google-icon {
        position: absolute;
        margin-top: 11px;
        margin-left: 11px;
        width: 18px;
        height: 18px;
    }
    .btn-text {
        float: right;
        margin: 11px 30px 0 0;
        color: $white;
        font-size: 14px;
        letter-spacing: 0.2px;
    }
    &:hover {
        box-shadow: 0 0 6px $google-blue;
    }
    &:active {
        background: $button-active-blue;
    }
}
